<template>
	<!-- 注册页面 -->
	<view class="padding">
		<view class="title">
			找回密码
		</view>
		<view class="text-gray text-df margin-top">
			请输入手机号码、新密码、短信验证码
		</view>
		<view class="form">
			<view class="username">
				<input type="number" placeholder="请输入手机号" v-model="mobile" placeholder-style="color: #C2C2C2;font-size:32rpx;" />
			</view>
			<view class="password">
				<input maxlength="12" placeholder="请输入密码(6~12位数字加字母)" v-model.trim="password" password="true" placeholder-style="color: #C2C2C2;font-size:32rpx;" />
			</view>
			<view class="code">
				<view class="get-code text-df" :style="{ color: getCodeBtnColor }" @click.stop="getCode()">{{ getCodeText }}</view>
				<input type="number" placeholder="验证码" v-model="code" placeholder-style="color: #C2C2C2;font-size:32rpx;" />
			</view>
			<view class="btn_next" style="margin-top: 200rpx;background-color: #0289FF;" @tap="doReg">重置密码</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				mobile: '',
				code: '',
				password: '',
				getCodeText: '获取验证码',
				getCodeBtnColor: '#50AEFF',
				getCodeisWaiting: false,
				code_id: 0
			};
		},
		onLoad() {},
		methods: {
			getCode() {//获取验证码
				uni.hideKeyboard();
				if (this.getCodeisWaiting) {
					return;
				}
				if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.mobile)) {
					uni.showToast(
						!this.mobile ?
						{
							title: '请输入手机号码',
							icon: 'none'
						} :
						{
							title: '请填写正确手机号码',
							icon: 'none'
						}
					);
					return false;
				}
				
				this.getCodeText = '发送中...';
				this.getCodeisWaiting = true;
				this.getCodeBtnColor = '#999999';
				this.$Request
					.post(this.$api.common.getverifycode, {
						mobile: this.mobile,
						type: 2
					})
					.then(res => {
						if (res.code == 1) {
							this.code_id = 1;
							this.setTimer();
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
							this.getCodeText = '获取验证码';
							this.getCodeBtnColor = '#999';
							this.getCodeisWaiting = false;
						}
					});
			},
			setTimer() {
				let holdTime = 60;
				this.getCodeText = '重新获取(60)';
				this.Timer = setInterval(() => {
					if (holdTime <= 0) {
						this.getCodeisWaiting = false;
						this.getCodeBtnColor = '#999';
						this.getCodeText = '获取验证码';
						clearInterval(this.Timer);
						return;
					}
					this.getCodeText = '重新获取(' + holdTime + ')';
					holdTime--;
				}, 1000);
			},
			doReg() {//重置密码
				uni.hideKeyboard();

				//模板示例部分验证规则//按照测试文档手机号为空提示请输入手机号
				if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.mobile)) {
					uni.showToast(
						!this.mobile ?
						{
							title: '请输入手机号码',
							icon: 'none'
						} :
						{
							title: '请填写正确手机号码',
							icon: 'none'
						}
					);
					return false;
				}
				var reg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$/;
				if (!reg.test(this.password)||this.password.length<6||this.password.length>12) {
					uni.showToast({
						title:'请输入6-12位，字母、数字组合的密码',
						icon:'none'
					});
					return false;
				}
				if (!this.code_id) {
					uni.showToast({
						title: '请先获取验证码',
						icon: 'none'
					});
					return false;
				}
				if (!this.code) {
					uni.showToast({
						title: '请输入验证码',
						icon: 'none'
					});
					return false;
				}
				
				uni.showLoading({
					title: '提交中...'
				});

				this.$Request
					.post(this.$api.user.user_forget_password, {
						mobile: this.mobile,
						code: this.code,
						password: this.password
					})
					.then(res => {
						uni.hideLoading();
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
						if (res.code == 1) {
							setTimeout(() => {
								// this.$mRouter.push({
								// 	route: this.$mRoutesConfig.login
								// });
								uni.navigateBack({})
							}, 600);
						}
					});
			},
			toLogin() {
				this.$mRouter.push({
					route: this.$mRoutesConfig.login
				});
			}
		}
	};
</script>
<style lang="scss">
	@font-face {
		font-family: 'HMfont-login';
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.icon {
		font-family: 'HMfont-login' !important;
		font-style: normal;
		color: #ffffff;

		&.weixin {
			&:before {
				content: '\e615';
			}
		}

		&.qq {
			&:before {
				content: '\e752';
			}
		}

		&.weibo {
			&:before {
				content: '\e63d';
			}
		}

		&.xiaomi {
			&:before {
				content: '\e661';
			}
		}
	}

	page {
		background: #fff;
		height: calc(100%-44upx);
	}


	.title {
		font-size:48rpx;
		margin:52rpx 0 0rpx ;
		font-weight: bold;
		letter-spacing: 1upx;
	}

	.form {
		.code {
			position: relative;

			.get-code {
				position: absolute;
				height: 90upx;
				display: flex;
				align-items: center;
				justify-content: center;
				right: 0;
				z-index: 3;


			}
		}
	}

	.form {
		// padding: 0 7%;
		font-size: 30upx;
		margin:60upx auto 0;
		.username,
		.password,
		.code {
			width: calc(100%);
			height: 120upx;
			display: flex;
			align-items: center;
			margin-bottom: 26upx;
			// border-bottom: 1rpx solid #ededed;

			input {
				width: 100%;
				height: 90upx;
				font-size: 32rpx;
				letter-spacing: 2upx;
			}
		}
		.icon_phone{
			width: 29rpx;
			height: 38rpx;
		}
		.icon_password{
			width: 30rpx;
			height: 38rpx;
		}
		.icon_code{
			width: 34rpx;
			height: 38rpx;
		}
		
	}

	.oauth {
		position: absolute;
		bottom: 50upx;
		width: 100%;

		.text {
			width: 100%;
			height: 60upx;
			display: flex;
			justify-content: center;
			align-items: center;
			color: rgba($color: #ffffff, $alpha: 0.8);
			font-size: 28upx;
		}

		.list {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 20upx 0;

			.icon {
				font-size: 80upx;
				margin: 0 30upx;
			}
		}
	}

	.blue-yy {
		color: #CCCCCC
	}
	
</style>
